.sidenav {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: #111;
  overflow-x: hidden;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  padding-top: 60px;
  text-align:center;
}

.sidenav a {
  padding: 50px 0px;
  text-decoration: none;
  font-size: 69px;
  color: #818181;
  display: block;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;

}

.sidenav a:hover{
  color: #f1f1f1;
}

.sidenav .cross {
  position: absolute;
  top: 5px;
  right: 25px;
  font-size: 69px;
  color: white; 
  cursor: pointer;
}

@media screen and (max-height: 450px) {
.sidenav {padding-top: 15px;}
.sidenav a {font-size: 18px;}
}

.hamburger {
  position: fixed;
  top: 5px;
  right: 10px;
  color: white;
  font-size: 50px;
  cursor: pointer;
}


